.contents{
    display: flex;
    height: 400px;
    width: 100%;
    background-color: rgb(240, 216, 216);
    padding-bottom: 20px;
}

.movie{
    overflow: hidden;
    position: relative;
    width: 23%;
    height: 100%;

    border: 20px solid transparent;
    box-sizing: border-box;
    -ms-box-sizing: border-box;
    -moz-box-sizing: border-box;
    -webkit-box-sizing: border-box;
    z-index: 1;
}

.movie_data{
    overflow: hidden;
    position: relative;
    width: 77%;
    height: 100%;
    float: left;
    border: 10px solid transparent;
    box-sizing: border-box;
    z-index: 0;


}