body{
    background-color: rgb(255, 255, 255);
    height: 100%;
    margin: 0;
    padding: 0;
    font-family: 'SpoqaHanSans-Bold', "굴림", Gulim,"돋움", Dotum;
    font-size: 16px;
    color: #212121;
    line-height: 1.6;
}

#root{
    width: 100%;
    background: #ffffff;
}

.movies{
    max-width: 1440px;
    margin: 0 auto;
    padding: 10px;
    box-sizing: border-box;
    -ms-box-sizing: border-box;
    -moz-box-sizing: border-box;
    -webkit-box-sizing: border-box;
}